import React, { useState } from 'react'
import styled from 'styled-components'

const Div = styled.div`

`
const Link = styled.a`
    display: block;
    width: 36px;
    height: 36px;
    cursor: pointer; 
    border-radius: 50%;
    transition: .2s linear;
    transform: scale(${props => props.isHover ? '1.1' : '1'});
`
const Img = styled.div`
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #999;
    background-image: url(${props => props.image});
    background-position: center;
    background-repeat: no-repeat;
    background-size: 17px 17px;
`
const Social = (props) => {
    const [isHover, setIsHover] = useState(false)

    const linkHover = event => {
        console.log(isHover);
        setIsHover(true)
    }
    const linkOut = event => {
        console.log(isHover);
        setIsHover(false)
    }
    return (
       <Div >
           <Link isHover={isHover} onMouseOut={linkOut} onMouseOver={linkHover}  href={props.link}>
               <Img {...props}></Img>
           </Link>
       </Div>
    )
}

export default Social