import React from 'react'
import img from '../../../../assets/global/advantage/cost/cost.png'
import styled from 'styled-components'

const Div = styled.div`
    display: flex;
    flex-direction: ${props => props.flexDirectionItem ? props.flexDirectionItem : 'column'};
    align-items: center;
    @media(max-width: 991px){
        flex: 1 1 30%;
        margin-bottom: ${props => props.flexDirectionItem ? '15px' : '0'};
    }
    @media(max-width: 768px){
        flex: 1 1 100%;
    }
    @media(max-width: 525px){
        flex: 1 1 auto;
        margin-top: 0;
        margin-bottom: 15px;
    }
`
const Img = styled.div`
    max-width: 105px;
    flex: 1 1 auto;

    margin-right: ${props => props.flexDirectionItem ? '5px' : 0};
    display: flex;
    justify-content: center;
    align-content: center;
    img{
        display: block;
        width: 100%;
    }
`
const Text = styled.div`
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 200%;
    color: #333333;
    span{
        display: ${props => props.extraText ? 'block' : 'none'};
        font-weight: bold;
    }
`

const Cost = (props) => {
    return(
        <Div {...props}>
            <Img {...props}>
                <img src={img} alt="Доставка" />
            </Img>
            <Text {...props}>Только честные цены<span>Нашли дешевле? Снизим цену!</span></Text>
        </Div>
    )
}

export default Cost