import React, { useState } from 'react'
import { NavLink } from 'react-router-dom';
import styled from 'styled-components'

const Div = styled.div`
    background-image: url('${props => props.image}');
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    grid-area: ${props => props.area};
    
`
const Content = styled.div`
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 50px;
    left: 45px;
    @media(max-width: 991px){
        bottom: 30px;
        left: 20px;
    }
`
const Text = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 28px;
    text-transform: uppercase;

    color: #fff;
    margin-bottom: 15px;
`
const Button = styled.button`
        cursor: pointer;
        border: none;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 170px;
        height: 40px;
        background: #FFCC01;

        display: block;
        transition: .2s linear;
        transform: scale(${props => props.isHover ? '1.05' : '1'}); 

        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 100%;
        text-transform: uppercase;
        color: #333333;
        position: relative;
        ::after{
            content: '';
            position: absolute;
            display: block;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);

            width: 0;
            height: 0;
            border-top: 6px solid transparent;
            border-bottom: 6px solid transparent;
            border-left: 10px solid #333333;
        }
`


const ItemNet = (props) => {
    const [isHover, setIsHover] = useState(false)

    const linkHover = event => {
        console.log(isHover);
        setIsHover(true)
    }
    const linkOut = event => {
        console.log(isHover);
        setIsHover(false)
    }
    return (
        <Div {...props}>
            <Content>
                <Text> {props.text} </Text>
                <NavLink   to='/news'>
                    <Button isHover={isHover} onMouseOut={linkOut} onMouseOver={linkHover}>читать статью</Button>
                </NavLink>
            </Content>
        </Div>
    )
}

export default ItemNet