import React, { useState } from 'react'
import { NavLink } from 'react-router-dom';
import styled from "styled-components";

const Li = styled.li`
    flex: 0 1 20%;
    margin-bottom: 30px;
    padding: 0 5px;
    transition: .3s linear;
    border-radius: 10px;

    padding-top: 5px;
    background-color: ${props => props.isHover ? '#ffffdf' : null};

    :last-child{
        margin-right: auto;
    }
    @media(max-width: 1198px){
        flex: 0 1 25%;
    }
    @media(max-width: 991px){
        flex: 0 1 25%;
    }
    @media(max-width: 792px){
        flex: 0 1 33.33%;
    }
    @media(max-width: 525px){
        flex: 0 1 50%;
        padding: 0;
    }
    a{
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;
    }
`

const Img = styled.div`
    width: 110px;
    flex: 1 1 100%;
    margin-bottom: 5px;
    img{
        display: block;
        width: 100%;
    }
`
const Text = styled.div`
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;

    color: #888888;
`


const Item = (props) => {
    const [isHover, setIsHover] = useState(false)
    const linkHover = event => {
        console.log(isHover);
        setIsHover(true)
    }
    const linkOut = event => {
        console.log(isHover);
        setIsHover(false)
    }
    return(
        <Li isHover={isHover} onMouseOut={linkOut} onMouseOver={linkHover}>
            <NavLink  to={props.link}>
                <Img>
                    <img src={props.image} alt="Bike" />
                </Img>
                <Text>{props.title}</Text>
            </NavLink>
        </Li>
    )
}

export default Item
