import { MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons';
import React, { useState } from 'react'
import styled from "styled-components";

const Div = styled.div`
    margin-right: 7px;
    @media (max-width: 991px){
        display: none;
    }
`

const Button = styled.button`
    width: 200px;
    height: 54px;
    background: #FFCC01;
    transition: .2s linear;
    transform: scale(${props => props.isHover ? '1.05' : '1'});
    @media (max-width: 1198px){
        width: 54px;
    }
`
const ButtonDiv = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`
const Text = styled.div`
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #333333;
    margin-left: 24px;
    @media (max-width: 1198px){
        display: none;
    }
    
`
const BtnIconStyle = {fontSize: '22px'}

const CatalogBtn = (props) => {
    const [isHover, setIsHover] = useState(false)

    const linkHover = event => {
        setIsHover(true)
    }
    const linkOut = event => {
        setIsHover(false)
    }

    return (
        <Div >
            <Button isHover={isHover} onMouseOut={linkOut} onMouseOver={linkHover} onClick={ props.showModal }>
                <ButtonDiv>
                    {props.isActive ? <MenuUnfoldOutlined style={ BtnIconStyle }/> : <MenuFoldOutlined style={ BtnIconStyle }/>}
                    <Text> Каталог </Text>
                </ButtonDiv>
                
            </Button>
        </Div>
        
    )
}

export default CatalogBtn